import React, { useState, useEffect } from 'react';
import construction from '../../assets/images/Construction.jpg';
import fastighet from '../../assets/images/Fastighet.jpg';
import hotell from '../../assets/images/Hreception.jpg';
import securit from '../../assets/images/Securityguard.jpg';

const CarouselItem = ({ image, title, description }) => (
    <div className="relative">
        <div className="absolute w-full top-0 left-0 bg-black bg-opacity-60 p-4">
            <div className="text-white">
                <h2 className="text-xl font-semibold mb-2">{title}</h2>
                <p className="text-lg">{description}</p>
            </div>
        </div>
        <img src={image} alt="carousel-item" className="rounded-lg shadow-lg" />
    </div>
);

const MyCarousel = () => {
    const images = [
        {
            src: securit,
            title: "Skerhetsbranschen",
            description: "Guardia anvnds av skerhetsproffs p flera stora fretag fr att effektivt hantera sin inventering av nycklar och skerhetsutrustning."
        },
        {
            src: hotell,
            title: "Hotellbranschen",
            description: "Oavsett om du behver lna eller lna ut nycklar och utrustning, erbjuder Guardia en anvndarvnlig lsning fr hotellbranschen."
        },
        {
            src: fastighet,
            title: "Fastighetsbranschen",
            description: "Guardia frenklar processen att lna och spra nycklar, vilket gr det till ett idealiskt verktyg fr fastighetsfrvaltningsfretag."
        },
        {
            src: construction,
            title: "Byggbranschen",
            description: "Till och med kebabteknikern Ahmed frlitar sig p Guardia fr sina dagliga behov. Det r ett mngsidigt verktyg fr att hantera nycklar och utrustning p byggarbetsplatser."
        },
    ];

    const [currentIndex, setCurrentIndex] = useState(0);

    useEffect(() => {
        const interval = setInterval(() => {
            setCurrentIndex((prevIndex) => (prevIndex === images.length - 1 ? 0 : prevIndex + 1));
        }, 6000);

        return () => clearInterval(interval);
    }, [images.length]);

    const goToSlide = (index) => {
        setCurrentIndex(index);
    };

    return (
        <div className="relative mt-20">
            <div className="flex gap-4 overflow-hidden">
                <div className="flex transition-transform duration-300" style={{ transform: `translateX(-${currentIndex * 100}%)` }}>
                    {images.map((image, index) => (
                        <div className="min-w-full snap-center" key={index}>
                            <CarouselItem image={image.src} title={image.title} description={image.description} />
                        </div>
                    ))}
                </div>
            </div>
            <div className="absolute bottom-0 left-1/2 transform -translate-x-1/2 flex space-x-2 mb-2">
                {images.map((_, index) => (
                    <button
                        key={index}
                        className={`h-3 w-3 rounded-full ${currentIndex === index ? 'bg-blue-500' : 'bg-gray-300'}`}
                        aria-label={`G till bild ${index + 1}`}
                        onClick={() => goToSlide(index)}
                    ></button>
                ))}
            </div>
        </div>
    );
};

export default MyCarousel;
